.container {
  text-align: center;
  padding: 20px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  position: relative;
  color: rgb(25, 24, 23);
}

.movieList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.loading {
  font-size: 24px;
  color: #555;
  padding: 200px;
}
.title {
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  color: #333;
}
.summary {
  font-size: 12px;
}
.img {
  width: 100px;
  height: auto;
}
